  }
}
.nav-item__link {
  font-family: Lexend;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: inherit;
  transition: all 0.3s ease-in-out;
}
.nav-item__link:hover {
  color: var(--main-blue);
  text-decoration: underline;
}

.blog {
