}

.btn {
  display: block;
  height: 48px;
  padding: 14px;
  border: 2px solid #9DADF2;
  border-radius: 24px;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  color: #fff;
  text-transform: uppercase;
}
.btn_type_active {
  border: none;
  background: var(--main-blue);
}

.simplebar-track {
  top: 96px !important;
  right: 16px;
  width: 8px !important;
  height: 400px !important;
}
@media (max-width: 576px) {
  .simplebar-track {
    top: 50px !important;
    right: 6px !important;
    width: 4px !important;
    height: 300px !important;
  }
}
.simplebar-track::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 8px;
  height: 100%;
  background: rgba(255, 255, 255, 0.1215686275);
  border-radius: 10px;
}
@media (max-width: 576px) {
  .simplebar-track::before {
    width: 4px;
  }
}
.simplebar-scrollbar {
  width: 8px;
  background: var(--main-blue);
  border-radius: 10px;
}
@media (max-width: 576px) {
  .simplebar-scrollbar {
    width: 4px;
  }
}
.simplebar-scrollbar::before {
  display: none;
}

@media (max-width: 576px) {
  .header {
    position: relative;
    z-index: 10;
  }
}
.header-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 16px;
  padding: 24px 48px;
  border: 4px solid var(--main-blue);
  border-radius: 64px;
  z-index: 10;
}
@media (max-width: 1670px) {
  .header-wrapper {
    justify-content: flex-start;
  }
}
@media (max-width: 576px) {
  .header-wrapper {
    position: initial;
    padding: 10px 21px;
    border: 2px solid #478BF9;
  }
}
.header-buttons {
  display: flex;
  align-items: center;
}
@media (max-width: 1670px) {
  .header-buttons {
    margin-left: auto;
  }
}
@media (max-width: 576px) {
  .header-buttons {
    justify-content: flex-end;
  }
}
@media (max-width: 1670px) {
  .header__btn {
    display: none;
  }
}
.header__btn:not(:last-child) {
  margin-right: 14px;
}
.header__btn_type_language {
  margin-right: 14px;
  display: flex;
  align-items: center;
}
@media (max-width: 1670px) {
  .header__btn_type_language {
    margin-right: 0;
  }
}
.header__btn_type_icon {
  display: block;
  width: 16px;
  height: 16px;
  margin-right: 8px;
}
.header__btn_text {
  display: none;
}
.header__btn_text.visible {
  display: block;
}
.header__btn_type_mobile {
  display: none;
}
@media (max-width: 1670px) {
  .header__btn_type_mobile {
    display: block;
    width: 100%;
    text-align: center;
  }
  .header__btn_type_mobile:not(:last-child) {
    margin-bottom: 5px;
  }
}
.header-burger {
  display: none;
  height: 20px;
  cursor: pointer;
}
@media (max-width: 1670px) {
  .header-burger {
    display: block;
    margin-left: 12px;
  }
}
.header-burger__line {
  position: relative;
  display: block;
  width: 21px;
  transform: translateY(6px);
  border-bottom: 2px solid #fff;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
}
.header-burger__line.is-active {
  border: none;
}
.header-burger__line.is-active::after {
  top: 3px;
  transform: rotate(45deg);
}
.header-burger__line.is-active::before {
  bottom: -5px;
  transform: rotate(-45deg);
}
.header-burger__line::before {
  content: "";
  position: absolute;
  bottom: 4px;
  left: 0;
  width: 100%;
  height: 2px;
  border-bottom: 2px solid #fff;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
}
.header-burger__line::after {
  content: "";
  position: absolute;
  top: 6px;
  left: 0;
  width: 100%;
  height: 2px;
  border-bottom: 2px solid #fff;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
}
.header-nav {
  transition: transform 0.3s ease-in-out;
}
@media (max-width: 1670px) {
  .header-nav {
    position: absolute;
    top: 104px;
    right: 0;
    display: none;
    padding: 10px;
    background: #110A1D;
    z-index: 10;
  }
}
@media (max-width: 576px) {
  .header-nav {
    top: 90px;
    width: 100%;
  }
}
.header-nav.is-active {
  display: block;
  animation: smoothNavbarShow 0.3s ease-in-out;
}
@media (max-width: 1670px) {
  .header-nav__list {
    flex-direction: column;
    margin-bottom: 30px;
  }
}
@media (max-width: 1670px) {
  .header-nav__item:not(:last-child) {
    margin: 0 0 5px 0;
  }
}

@keyframes smoothNavbarShow {
  0% {
    transform: translateX(210%);
    opacity: 0;
  }
  100% {
    transform: none;
    opacity: 1;
  }
}
.banner {
  position: relative;
}
.banner-bg {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}
@media (max-width: 1670px) {
  .banner-bg {
    display: none;
