@media (max-width: 576px) {
  .banner-bg {
    top: -160px;
    display: flex;
    flex-direction: column;
  }
}
.banner-bg::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 200px;
  background: #000;
  filter: blur(100px);
}
@media (max-width: 576px) {
  .banner-bg::before {
    display: none;
