}
@media (max-width: 480px) {
  .banner-bg__frame_one {
    animation: translateLeft 7s linear infinite;
  }
}
.banner-bg__frame_two {
  animation: translateBottom 5s linear infinite;
  margin: 0 16px;
  background-image: url("../resources/images/banner-bg-frame2.png");
}
@media (max-width: 576px) {
  .banner-bg__frame_two {
    margin: 8px 0;
    animation: translateRight 5s linear infinite;
    background-image: url("../resources/images/banner-bg-frame2-mobile.png");
  }
}
@media (max-width: 480px) {
  .banner-bg__frame_two {
    animation: translateRight 7s linear infinite;
  }
}
.banner-bg__frame_three {
  animation: translateTop 5s linear infinite;
  background-image: url("../resources/images/banner-bg-frame3.png");
}
@media (max-width: 576px) {
  .banner-bg__frame_three {
    animation: translateLeft 5s linear infinite;
    background-image: url("../resources/images/banner-bg-frame3-mobile.png");
  }
}
@media (max-width: 480px) {
  .banner-bg__frame_three {
    animation: translateLeft 7s linear infinite;
  }
}
.banner-wrapper {
