@keyframes translateTop {
  0% {
    background-position-y: 0;
  }
  50% {
    background-position-y: 100%;
  }
  100% {
    background-position-y: 0;
  }
}
@keyframes translateBottom {
  0% {
    background-position-y: 0;
  }
  50% {
    background-position-y: -100%;
  }
  100% {
    background-position-y: 0;
  }
}
@keyframes translateLeft {
  0% {
    background-position-x: 0;
  }
  50% {
    background-position-x: 100%;
  }
  100% {
    background-position-x: 0;
  }
}
@keyframes translateRight {
  0% {
    background-position-x: 0;
  }
  50% {
    background-position-x: -100%;
  }
  100% {
    background-position-x: 0;
  }
}
